import React, { useState, useRef } from "react";
import { toast } from "react-toastify";
import QRCode from "react-qr-code";
import html2canvas from "html2canvas";
import {
  Button,
  Container,
  Grid,
  InputAdornment,
  TextField,
  Typography,
} from "@mui/material";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import DownloadIcon from "@mui/icons-material/Download";

const QrCode = () => {
  const [qrValue, setQrValue] = useState("");
  const qrCodeRef = useRef(null);

  const handleChange = (e) => {
    setQrValue(e.target.value);
  };

  const handleCopy = () => {
    navigator.clipboard.writeText(qrValue);
    toast.success("QR Code value copied", { position: "top-center" });
  };

  const handleScreenshot = () => {
    const qrCodeElement = qrCodeRef.current;

    const options = {
      backgroundColor: "#ffffff",
      width: 400,
      height: 400,
      scale: 4,
    };

    html2canvas(qrCodeElement, options).then((canvas) => {
      const link = document.createElement("a");
      link.download = "qrcode.png";

      // Create a new canvas with the specified dimensions
      const paddedCanvas = document.createElement("canvas");
      paddedCanvas.width = options.width;
      paddedCanvas.height = options.height;
      const ctx = paddedCanvas.getContext("2d");

      // Calculate the position to center the QR code
      const qrCodeX = (paddedCanvas.width - qrCodeElement.offsetWidth) / 2;
      const qrCodeY = (paddedCanvas.height - qrCodeElement.offsetHeight) / 2;

      // Draw the white background with padding
      ctx.fillStyle = options.backgroundColor;
      ctx.fillRect(0, 0, paddedCanvas.width, paddedCanvas.height);

      // Draw the QR code on the padded canvas
      ctx.drawImage(
        canvas,
        qrCodeX,
        qrCodeY,
        qrCodeElement.offsetWidth,
        qrCodeElement.offsetHeight
      );

      // Convert the padded canvas to data URL and download the image
      link.href = paddedCanvas.toDataURL("image/png");
      link.click();
    });
  };

  return (
    <Container>
      <Grid
        textAlign="center"
        style={{ border: "2px solid #f2f2f2", padding: 24, borderRadius: 4 }}
      >
        <Typography variant="h5" fontWeight="bold">
          QR Code Generator
        </Typography>
        <div
          style={{
            height: "auto",
            margin: "0 auto",
            maxWidth: 400,
            width: "100%",
          }}
        >
          <div ref={qrCodeRef}>
            <QRCode
              size={256}
              style={{ height: "auto", maxWidth: "100%", width: "100%" }}
              value={qrValue}
              viewBox={`0 0 256 256`}
            />
          </div>

          <TextField
            type="text"
            value={qrValue}
            onChange={handleChange}
            placeholder="Enter Your QR Code Text"
            style={{ maxWidth: "100%", width: "100%" }}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <Button
                    disabled={!qrValue}
                    color="primary"
                    onClick={handleCopy}
                  >
                    <ContentCopyIcon />
                  </Button>
                </InputAdornment>
              ),
            }}
          />

          <Button
            variant="contained"
            color="primary"
            onClick={handleScreenshot}
            disabled={!qrValue}
            style={{ maxWidth: "100%", width: "100%", marginTop: 6 }}
          >
            <DownloadIcon /> Download QR Code
          </Button>
        </div>
      </Grid>

      <Grid textAlign="center" pt={6}>
        <Typography variant="body2">
          &copy;2024. All Rights Reserved. <br />
          Developed and Designed by Omar Barghashoon.
        </Typography>
      </Grid>
    </Container>
  );
};

export default QrCode;
