import React from "react";
import { Route, Routes } from "react-router-dom";
import QrCode from "./QrCode";

const Router = () => {
  return (
    <Routes basename="/">
      <Route path="/" element={<QrCode />} />
      <Route path="*" element={<QrCode />} />
    </Routes>
  );
};

export default Router;
